export function signUserIn(username,password,fn){
                
 db.get('staff').then(function (dot) {
   
   let doc = JSON.parse(dot.staff);
                    
   for(let j = 0 ; j< doc.length ; j++){
              
    if(doc[j].name == username && doc[j].password == password){
                    
    let user = doc[j];
    
   return fn(user);
                
    }else if((j+1) == doc.length){
        
     return fn(false);
     
    }
                    
   }
        
        
   }).catch(function (err) {
     console.log(err);
        
    });
 
}

