export function reset(module,fn){
    //This resets a specific id/module in pouchdb to []
    //i.e it could reset staff,tickets etc to []
    db.get(module).then(function (doc) {
        
        console.log(doc[module]);
        
        if(JSON.parse(doc[module])[0] == null){
            
                
                fn(100);
        }else{
            
            var just =  [] ;
            doc[module] = JSON.stringify(just);  
            fn(200);
           
        
        
        }
        
        if(db.put(doc)){
        console.log (doc);
            
        }else{
            console.log("an error occured");    
        }
        
        },function (err) {
        
        console.log(err);
        
        if(err.name=='not_found'){
        
        console.log(err);
        
        fn(400)
        
                            }
        }); 
        
        
        
    
}

