export function getAttachment(id,spe_id,fn){
    db.getAttachment(id, spe_id, function(err, blob_buffer) {
        if (err) {
            return fn({});
        } else{
           let  result = URL.createObjectURL(blob_buffer);
           return fn(result);
        
        }
        });
}

