export function sellModule(module,campname,submod,itemname,p,quantity,fn){
            //call the addModule to add to tickets(using a for loop)
            // when this id done it will then
            //insert the campus doc back, this will have the new slots represented
            let just,totSold = []; 
            db.get(module).then(function (doc) {
             
            console.log(doc[module]);
            
            if(JSON.parse(doc[module])[0] == null){
                
                return fn({err:'not_found',value:false});
            }else{
                
               let length = JSON.parse(doc[module]).length;
               console.log(JSON.parse(doc[module]));
               console.log('I am length : ',length);
               for (let i=0; i< length;i++){
               console.log(JSON.parse(doc[module])[i].name,'  l ', campname);
                  if(JSON.parse(doc[module])[i].name == campname){
                      let movie = JSON.parse(doc[module])[i].movies;
                      let movlen = movie.length;
                      if(movie[0] == null){
                        
                      return fn({err:'not_found',value:false});
    
                      }else{
                        console.log("Inside else ");
                      for(let j=0; j< movlen; j++){
                        console.log(movie[j].name,'  l ', itemname);
                        
                        if(movie[j].name == itemname){
                           
                           just = JSON.parse(doc[module]);
                           let updatearray = just[i].movies[j] ;
                           let slots = updatearray.slots;
                           if(slots < quantity){
                            return fn({err:'under',value:false});
                            
                           }
                           let remSlot = Number(slots-(quantity));
                           for(let k=0;k<quantity;k++){
                            p.name = generateId(campname);
                            totSold[k]={name:p.name,type:p.type,mov_name:p.mov_name,category:p.category,price:p.price,vDate:p.vDate,vTime:p.vTime,dateadded:p.dateadded,usersold:p.usersold};
                                                     
                            if((k+1) == quantity){
                                console.log(totSold);
                                addModuleArray('ticket',totSold,function(res){
                                    if(res){
                                        
                                        just[i].movies[j].slots = remSlot;
                                           
                                            doc[module] = JSON.stringify(just);  
                                            if(db.put(doc)){
                                                console.log (doc);
                                                return fn({data:totSold,value:true});  
                                                }else{
                                                    console.log("Error");
                                                    return fn({err:'Error! Please try again later.',value:false});
                                                    
                                                   
                                                }
                                      
                                    }else{
                                        console.log("Error");
                                        return fn({err:'Error! Please try again later.',value:false});
                                        
                                    }
                                });
                            }
                            
                           
                           }
                          
                        }else if((j+1) == movlen){
                            return fn({err:'not_found',value:false});
                            
                        }
                    }
              }
              
            }
            
            }
        }
            
            
            },function (err) {
            
            console.log(err);
            
            if(err.name=='not_found'){
            
            console.log(err);
            
            return fn({err:'Error!',value:false});
            
                                }
            }); 
            
               
}

function addModuleArray(module,array,fn){
    console.log(array);
    db.get(module).then(function (doc) {
        let just; 
        console.log(doc[module]);
        
        if(JSON.parse(doc[module])[0] == null){
           console.log('Inside addModArr for loop');
           just =  JSON.parse(doc[module]) ;
           
           for(let i=0;i<array.length;i++){
            just.push(array[i]);
           
           } 
            doc[module] = JSON.stringify(just);  
            
        }else{
            
           let length = JSON.parse(doc[module]).length;
           console.log(JSON.parse(doc[module]));
           just =  JSON.parse(doc[module]) ;
           for (let j=0; j< array.length;j++){
            for (let i=0; i< length;i++){
                
                      if(JSON.parse(doc[module])[i].name == array[j].name){
                          console.log(false);
                          break;
                  }
                    else if((i+1) == length){
                    
                    just.push(array[j]);
                     
                    }
                }
                 if((j+1) == array.length){
                    
                    doc[module] = JSON.stringify(just);  
                     
                    }
           }
           
        
        }
        
        if(db.put(doc)){
        console.log (doc);
        return fn(just);  
        }else{
            console.log("Error");
        }
        
        },function (err) {
        
        console.log(err);
        
        if(err.name=='not_found'){
        
        console.log(err);
        
        return fn(false);
        
                            }
        }); 
        
        
        

}

