let _ = require('lodash');
export function login(username,password){
    if(username.length <=4){
       return{
           feedback:'Username must be more than 4 characters',
           status:false
       }
    }else if(password.length <=3){
        return{
            feedback:'Password must be more than 3 characters',
            status:false
        }
     }else if(username.length >20){
            return{
                feedback:'Username must be less than 20 characters',
                status:false
            }
        
        }else{
            return{
                status:true
            }
        }
}

export function checkright(array,value){
    let result = _.includes(array,value);
    return result;
}

export function specify(array,prop,val){
    let result = array.filter( obj => {
            return obj[prop] == val;
    });
    return result;
}

