export function trim(value){
    String.prototype.trimAll = function(){
        return this.replace(/\s+/g,"");
    }

    let trimmed = value.trimAll();
    console.log("Trimmed to: ",trimmed);
    return trimmed;
}
export function generateId(ini){
    let alpha = ['aPi','bQj','cqt','dze','e','af','g','hP','x','lM9']
    let result = ini+'_'+ new Date().getTime()+alpha[Math.floor(Math.random()*9)]+Math.floor(Math.random() * (1035-987) + 87)+alpha[Math.floor(Math.random()*9)];
    return result;
    }